.App {
  margin: 0 0 0 0;
  padding: 0; }

.user_card {
  width: 400px;
  font-family: 'Grenze', serif;
  margin: 1em;
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(80%, rgba(0, 0, 0, 0.67)), to(rgba(0, 0, 0, 0.84)));
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.67) 80%, rgba(0, 0, 0, 0.84) 100%); }

.user_card header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: white;
  cursor: pointer;
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), to(rgba(0, 0, 0, 0.54)));
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.54) 100%); }

.user_card section {
  margin: 0; }

header .wrap {
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

header:hover .wrap {
  -webkit-transform: scale(1.05);
          transform: scale(1.05); }

.user-name {
  font-size: 2em; }

.user-img {
  width: 6em;
  height: 6em;
  border-radius: 50%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 9em;
  margin-right: 1em;
  margin-left: 1.5em; }

.user_info {
  list-style-type: none;
  font-size: 1.35em;
  color: white;
  margin: 0; }

@keyframes lds-cube {
  0% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@-webkit-keyframes lds-cube {
  0% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.lds-cube {
  position: relative; }

.lds-cube div {
  position: absolute;
  width: 80px;
  height: 80px;
  top: 10px;
  left: 10px;
  background: #e58ccc;
  -webkit-animation: lds-cube 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  animation: lds-cube 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  -webkit-animation-delay: -0.15s;
  animation-delay: -0.15s; }

.lds-cube div:nth-child(2) {
  top: 10px;
  left: 110px;
  background: #e73c1d;
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s; }

.lds-cube div:nth-child(3) {
  top: 110px;
  left: 110px;
  background: #382623;
  -webkit-animation-delay: 0s;
  animation-delay: 0s; }

.lds-cube div:nth-child(4) {
  top: 110px;
  left: 10px;
  background: #0fc124;
  -webkit-animation-delay: -0.05s;
  animation-delay: -0.05s; }

.lds-cube {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px); }

.user_list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center; }

.no_results {
  font-family: 'Grenze', serif;
  font-size: 5em; }

.form {
  font-size: 16px;
  position: relative;
  overflow: hidden; }

.form input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  outline: none;
  font-family: 'Grenze', serif;
  color: white;
  font-size: 1.5em; }

.form label {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-style: none;
  font-size: 1.5em;
  background: tranparent; }

.form label::after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  border-bottom: 3px solid white;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.content-name {
  position: absolute;
  bottom: 0px;
  left: 0px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.form input:valid + .label-name .content-name {
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%); }

.form input:focus + .label-name::after,
.form input:valid + .label-name::after {
  -webkit-transform: translateX(0%);
          transform: translateX(0%); }

.navbar {
